// stylelint-disable at-rule-empty-line-before

@import "variables";
@import "functions";
@import "mixins";
@import "sprites";
@import "fonts";
@import "vendor";
@import "base";
@import "helpers";

$input-padding-right: $input-padding-x * 2.5;

.btn {
	&:focus {
		box-shadow: 0 0 5px 0.1rem rgba(14, 49, 84, 0.25);
	}

	&:focus:active {
		box-shadow: 0 0 5px 0.1rem rgba(14, 49, 84, 0.25) !important;
	}
}

.section-title {
	min-height: 31px;
	background: $blue;
	color: white;
	display: flex;
	align-items: center;
	font-weight: 700;
	font-size: 16px;
	line-height: 17px;
	padding: 6px 12px;
	margin: 0 0 16px;

	&_tabs {
		margin: 0 0 12px;
	}
}

.nav-tabs {
	.nav-link {
		font-weight: 700;
		background: $nav-tabs-link-bg;
		border-color: $nav-tabs-link-border-color;
		padding: 0.25rem 0.75rem;
		position: relative;
		margin: 0;
		text-align: left;

		&:not(.active) {
			&:after {
				position: absolute;
				background: #fff;
				content: '';
				left: 0;
			}

			&:after {
				width: 100%;
				bottom: 0;
			}
		}
	}

	.nav-item {
		&:first-child {
			.nav-link {
				margin-left: 0;
			}
		}

		&:last-child {
			.nav-link {
				margin-right: 0;
			}
		}

		@include media-breakpoint-down(md) {
			flex: 1 1 auto;
		}

		@include media-breakpoint-up(lg) {
			width: 167px;
			max-width: 100%;
		}
	}

	&_space-small {
		.nav-link {
			margin: 0 ($nav-tabs-divider-width / 2);

			&:after {
				height: $nav-tabs-divider-width;
			}
		}
	}

	&_space-large {
		.nav-link {
			margin: 0 ($nav-tabs-divider-width / 2) * 2;

			&:after {
				height: $nav-tabs-divider-width * 2;
			}
		}
	}
}

.tab-content {
	background: $nav-tabs-link-bg;

	.tab-pane {
		&.active {
			background: $nav-tabs-link-active-bg;
		}

		&_find {
			padding: 17px 13px;
		}

		&_categories {
			padding: 20px 12px 10px;
		}
	}
}

.form-control {
	&_icon {
		background: url(../images/sprites/svg/chevron.svg) no-repeat $input-bg;
		background-position: right ($input-padding-right / 3.5) center;
		padding-right: $input-padding-right;
		background-size: 14px 7px;
	}

	&_icon_left {
		background: url(../images/sprites/svg/search.svg) no-repeat $input-bg;
		background-position: 12px center;
		padding-left: 50px;
		background-size: 25px;
	}

	&.is-invalid,
	&:invalid {
		&:focus {
			box-shadow: 0 0 5px 0.1rem $input-focus-box-shadow;
		}
	}

	&.dropdown-toggle {

		&:after,
		&:before {
			display: none;
		}

		@include hover-focus {
			outline: none !important;
		}
	}

	&-lg {
		border-color: $input-border-color-lg;
	}

	&_lg_default {
		height: $input-height-lg;
		font-size: $input-font-size-lg;
	}
}

.col_find_prod {
	@include media-breakpoint-up(xs) {
		@include make-col(12)
	}

	@include media-breakpoint-up(md) {
		@include make-col(6)
	}

	@include media-breakpoint-up(lg) {
		flex: 0 0 36.5%;
		max-width: 36.5%;
	}
}

.card-categories {
	margin: 0 0 1rem;
	display: flex;
	flex-direction: column;

	&__img-wrap {
		position: relative;
		margin: 0;

		&:before {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			content: '';
			opacity: 0;
			background: $blue;
			mix-blend-mode: multiply;
			transition: opacity 0.25s linear;
		}
	}

	&__caption {
		font-size: 13px;
		line-height: 14px;
		text-align: center;
		background: #FFFFFF;
		min-height: 31px;
		max-height: 100%;
		height: 100%;
		margin: 0;
		transition: background, color 0.25s linear;
		padding: 5px 10px;
	}

	&__img {
		margin: 0;
		width: 100%;
		height: 125px;
		object-fit: cover;

		// @include media-breakpoint-down(sm) {
		// 	height: 80px;
		// }
	}

	&:hover,
	&:focus {
		text-decoration: none;
	}

	&:hover &,
	&:focus & {
		&__img-wrap {
			&:before {
				opacity: 0.6;
			}
		}

		&__caption {
			background: $blue;
			color: white;
		}
	}
}

.form-group {
	position: relative;
}

.bootstrap-select {

	.filter-option {
		line-height: 1.3;
	}

	.dropdown-item {
		white-space: normal;
		padding: $dropdown-header-padding;

		&_title {
			@include font-size(14px);
			font-weight: bold;
		}

		&_cat {
			padding: 0 $dropdown-item-padding-x;
		}

		&.selected {
			@extend .dropdown-item.active;
		}

		@include hover-focus {
			&.active {
				color: white;
			}
		}

		span.check-mark {
			transform: translateY(-50%) !important;
			top: 50% !important;
			margin: -2px 0 0;
		}
	}

	.filter-option {
		display: flex;
		align-items: center;
	}

	&.form-control_lg {
		.dropdown-toggle {
			&:focus {
				@extend .form-control:focus;
			}

			@extend .form-control-lg;

			&.form-control_icon {
				.filter-option {
					width: calc(100% - 15px);
				}

				.filter-option-inner {
					width: 100%;
				}

				.filter-option-inner-inner {
					white-space: nowrap;
					text-overflow: ellipsis;
				}
			}
		}

		.form-control {
			@include font-size(18px)
		}

		.bs-searchbox {
			.form-control {
				height: $input-height-lg;
			}
		}
	}

	&.show {

		.form-control,
		&.form-control_lg .dropdown-toggle {
			@extend .form-control:focus;
		}
	}

	.bs-invalid {
		@extend .is-invalid;
	}

	.popover-header {
		padding: 5px 10px 0;
	}

	.no-results {
		white-space: normal;
	}

	.bs-select-all {
		display: none;
	}

	.bs-deselect-all {
		width: 100% !important;
	}
}

.see-more {
	text-decoration-line: underline;

	@include hover-focus {
		text-decoration: none;
	}

	&__text {
		&-close {
			display: none;
		}
	}

	&:not(.collapsed) {
		.see-more__text-show {
			display: none;
		}

		.see-more__text-close {
			display: inline;
		}
	}
}

.was-validated {
	.invalid-feedback {
		display: block;
	}

	.bootstrap-select {
		.bs-searchbox {
			.form-control {

				&:valid,
				&.is-valid {
					box-shadow: $input-box-shadow;
					border-color: $input-border-color;
					background: none;
					padding-right: $input-padding-x;

					&:focus {
						box-shadow: $input-focus-box-shadow;
					}
				}
			}
		}

		select:valid+.dropdown-toggle {
			border-color: $input-border-color;
		}
	}

	.form-control {

		&:valid,
		&.is-valid,
		&:valid:focus,
		&.is-valid:focus,
		&:invalid,
		&.is-invalid {
			box-shadow: $input-box-shadow;

			&:focus {
				box-shadow: $input-focus-box-shadow;
			}
		}

		&:valid,
		&.is-valid {
			border-color: $input-border-color;
			background-image: inherit;

			&:focus {
				border-color: $input-border-color;
			}
		}
	}
}

.modal {

	.close {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		right: 0;
		// padding: 0;
		margin: 0;
		outline: none;
		// @include font-size(60px);
		font-size: 60px;
		font-weight: normal;

		&_place {
			&_top {
				top: -16px;
				padding: 0;
				right: -7px;
				color: #fff;
				opacity: 1;
			}

			&_static {
				position: static;
				display: inline-block;
				line-height: 1.1;
				transform: none;
				float: none;
				vertical-align: middle;
			}
		}
	}

	.modal-header {
		position: relative;
	}

	&_request {
		color: $blue;

		.border-right {
			@include media-breakpoint-down(md) {
				border: none !important;
			}
		}

		@include media-breakpoint-down(md) {
			.modal-dialog {
				margin-top: 65px;
			}
		}
	}

	&__request-list {
		padding: 0;
		margin: 0;
		counter-reset: request-list;
		list-style: none;
		height: 235px;
		overflow-y: auto;
	}

	&__list-item {
		@extend .d-flex;
		@include font-size(16px);
		align-items: flex-start;
		font-weight: 500;

		&:not(:last-child) {
			padding: 0 0 2px;

			@include media-breakpoint-down(sm) {
				padding: 0 0 7px;
			}
		}

		&:before {
			counter-increment: request-list;
			content: counter(request-list)'.';
			display: inline-block;
			padding: 0 2px;
		}

		.close {
			@include font-size(40px);
			line-height: 23px;
		}
	}

	&_reset_border_lg {
		border: none;
	}

	textarea.form-control {
		height: 156px;
	}

	@include media-breakpoint-up(md) {
		&_is-visible-lg {
			position: relative;
			top: auto;
			right: auto;
			bottom: auto;
			left: auto;
			z-index: 1;
			display: block !important;
			overflow: visible;

			&:not(.show) {
				opacity: 1;
			}

			.modal-dialog:not(.show) {
				transform: none;
			}
		}

		&-dialog_width_full_lg {
			max-width: 100%;
		}
	}

	@include media-breakpoint-down(xs) {
		.modal-dialog {
			margin-top: 65px;
		}
	}

	&-title {
		&:not(h4) {
			@include font-size(32px);
		}
	}

	@include media-breakpoint-down(sm) {
		.btn.btn-block {
			margin: 0 0 1rem;
		}
	}
}

.custom-control {
	&_single {
		padding-left: $custom-control-indicator-size;
		.custom-control-label {
			&:before,
			&:after {
				left: -$custom-control-indicator-size;
			}
		}
	}
}

.custom-select {
	&.form-control-lg {
		height: 40px;
		border-radius: 0;
		@include font-size(18px);
	}
}

.product-list {
	&__text {
		@include font-size(16px);
		line-height: 1.1;
		color: $blue;

		@include media-breakpoint-down(sm) {
			@include font-size(18px);
			font-weight: 500;
		}
	}

	&__filter {
		@include media-breakpoint-up(md) {
			background: #E6E6E6;
			padding: 20px 15px;
		}
	}

	&__filter-title {
		@include font-size(20px);
		line-height: 1.2;
		color: $blue;
	}

	&__open-filter {
		@include font-size(18px);
		font-weight: bold;
		text-decoration: underline;
		color: $blue;
		line-height: 1;

		&-icon {
			height: 32px;
			width: 47px;
			fill: $blue;
		}

		&:hover {
			text-decoration: none;
		}
	}

	&__pages-count {
		list-style: none;
		padding: 0;
		margin: 0;

		@extend .d-flex;

		&__item {
			&-text {
				border-right: 1px solid #000;
				display: inline-block;
				padding: 2px 4px;
				line-height: 1;

				&.active {
					background: $blue;
					color: #fff;
				}
			}

			&:last-child {
				.product-list__pages-count__item-text {
					border-right: 0;
				}
			}

			a {
				text-decoration: underline;

				&:hover {
					text-decoration: none;
				}
			}

		}
	}

	@include media-breakpoint-down(md) {
		@include font-size(16px);
	}
}

.table-list {
	&__header {
		border-bottom: 1px solid #E6E6E6;

		.form-control.dropdown-toggle {
			padding: 0;
			border: none;
			color: $blue;
			font-weight: bold;
			height: auto;
			@include font-size(16px);

			&:hover,
			&:focus {
				box-shadow: none;
			}

			&.form-control_icon {
				padding-right: 25px;
				background-image: url(../images/sprites/svg/filter-stroke.svg);
				background-size: 16px 12px;
				background-position: center right;
			}
		}

		.show {
			.form-control.dropdown-toggle.form-control_icon {
				background-image: url(../images/sprites/svg/filter-solid.svg);
			}
		}

		.dropdown {
			&-menu {
				min-width: 150px;

				.dropdown-item {
					padding: 0 12px;
					white-space: nowrap;

					// &.selected {
					// 	background: $dropdown-link-active-bg;
					// }
				}
			}
		}

		.bootstrap-select:not(.show) {
			.is-selected {
				&.form-control_icon {
					background-image: url(../images/sprites/svg/filter-solid.svg);
				}
			}
		}
	}

	th,
	&__item {
		text-align: center;
		padding: 10px 6px;

		@include media-breakpoint-up(lg) {
			padding: 10px 12px;
		}

		@include media-breakpoint-between(sm, md) {
			padding: 10px 1px;

			.custom-control {
				margin: 0 7px 0 0;
			}
		}
	}

	&__title {
		font-weight: bold;
		font-size: 16px;
		line-height: 17px;
		color: $blue;
	}

	.figure {
		border: 1px solid #E6E6E6;
		// padding: 10px;

		&-img {
			object-fit: cover;
			height: 80px;
			width: 75px;
		}

		@include media-breakpoint-down(md) {
			// padding: 5px;

			&-img {
				width: 40px;
				height: 40px;
			}
		}
	}

	p {
		margin: 0;
	}

	&__item {
		&_link {
			a {
				text-decoration: underline;
				&:hover {
					text-decoration: none;
				}

				@include media-breakpoint-up(md) {
					max-width: 70px;
				}
			}
		}

		@include media-breakpoint-down(md) {
			&_tape {
				max-width: 132px;
			}
		}
	}

	@include media-breakpoint-up(md) {
		&__row {
			&:nth-child(even) {
				background: rgba(230, 230, 230, 0.5);
			}
		}
	}

	@include media-breakpoint-down(sm) {
		&__body {
			display: block;
		}

		&__header {
			display: none;
		}

		&__row {
			display: flex;
			flex-wrap: wrap;
			@include make-row;
			margin: 0;
		}

		&__item {
			display: block;
			padding-left: 0;
			padding-right: 0;
			@include font-size(18px);

			&_checkbox {
				width: 100%;
				text-align: left;
				font-weight: bold;
			}

			&_tape {
				display: none;
			}

			&_image {
				@include make-col-auto;
				margin-bottom: auto;

				.figure {
					&-img {
						width: 98px;
						height: 98px;
					}
				}
			}

			&_link {
				width: 50%;
				text-align: center;

				* {
					display: inline-block;
					max-width: 130px;
				}
			}

			&_text {
				display: none;

				&_mobile {
					text-align: left;
				}
			}
		}
	}
}

.modal-open-filter {
	@include media-breakpoint-up(md) {
		.modal-backdrop {
			display: none;
		}
	}
}

.pagination {
	.page-item {
		.page-link {
			border-radius: 0 !important;
			text-decoration: underline;
			@include font-size(19px);
			min-height: 40px;
			height: 100%;
			min-width: 40px;
			@extend .d-flex;
			@extend .align-items-center;
			@extend .justify-content-center;

			&__icon {
				fill: $blue;
				width: 20px;
				height: 20px;
			}

			&_more {
				text-decoration: none;
			}

			&:hover,
			&:focus {
				text-decoration: none;

				.page-link__icon {
					fill: currentColor;
				}
			}

			@include media-breakpoint-down(sm) {
				min-height: 30px;
				min-width: 30px;
			}
		}

		&:first-child,
		&:last-child {
			.page-link {
				text-decoration: none;
			}
		}

		&.active {
			.page-link {
				text-decoration: none;
				z-index: 0;
			}
		}

		&.disabled {
			.page-link__icon {
				fill: currentColor;
			}
		}
	}
}

.steps {
	list-style: none;
	display: flex;
	flex-wrap: wrap;
	padding: 0;
	counter-reset: counter-mobile;

	&__article {
		@include font-size(18px);
		color: $blue;
	}

	&__item {
		flex-grow: 1;

		&__counter {
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			@include font-size(139px);
			line-height: 1;
			font-weight: bold;
			color: white;
			pointer-events: none;
			display: none;

			@include media-breakpoint-up(md) {
				display: block;
			}
		}

		&__text {
			font-weight: 500;
			position: relative;

			@include media-breakpoint-up(md) {
				z-index: 1;
				width: calc(100% - 30%);
				@include font-size(29px);
			}

			@include media-breakpoint-down(sm) {
				@include font-size(18px);
				line-height: 1.1;
				font-weight: 700;
				text-transform: uppercase;
				width: 105px;
				display: flex;
				align-items: center;
				justify-content: center;
				text-align: center;
				flex-shrink: 0;
				background: #E6E6E6;
				margin: 0 15px 0 0;

				&:before {
					counter-increment: counter-mobile;
					content: counter(counter-mobile);
					position: absolute;
					left: 50%;
					transform: translateX(-50%);
					top: 0;
					display: block;
					font-weight: bold;
					font-size: 96px;
					text-align: center;
					color: #FFFFFF;
					z-index: 2;
					margin: 5px 0 0;
				}

				&-inner {
					display: block;
					position: relative;
					z-index: 3;
				}
			}

			&-mobile {
				@include font-size(16px);
				font-weight: 500;
				margin: 0;
				overflow: hidden;
				min-height: 89px;
				display: flex;
				align-items: center;
			}
		}

		&__link {
			display: flex;
			transition: background .25s linear;

			@include media-breakpoint-down(sm) {
				height: 100%;
			}

			@include media-breakpoint-up(md) {
				position: relative;
				z-index: 1;
				background: #E6E6E6;
				color: white;
				text-decoration: none;
				text-align: left;
				height: 100%;
				font-weight: 500;
				line-height: 1;
				text-transform: uppercase;
				color: #0E3154;
				padding: 14px 4px;
				align-items: center;
			}

			&:before,
			&:after {
				content: "";

				@include media-breakpoint-up(md) {
					display: block;
					border: inherit;
					position: absolute;
					left: -1em;
					right: -1em;
					background: inherit;
					z-index: -1;
					border-left: 3px solid white;
					width: 50%;
				}
			}

			@include media-breakpoint-up(md) {
				&:before {
					top: 0;
					bottom: 50%;
					border-bottom: none;
					transform: skewX(45deg);
				}

				&:after {
					top: 50%;
					bottom: 0;
					border-top: none;
					transform: skewX(-45deg);
				}
			}

			&:hover,
			&:focus,
			&:not(.collapsed) {
				outline: none;
				text-decoration: none;

				@include media-breakpoint-up(md) {
					background: #C4C4C4;
				}
			}

			@include media-breakpoint-up(md) {
				&:not(.collapsed) {
					pointer-events: none;
				}
			}

			@include media-breakpoint-down(sm) {
				&:not(.collapsed) {
					.steps__item__text {
						background: #C4C4C4;
					}

					&:before,&:after {
						border-top-color: #C4C4C4;
					}

					&:before {
						opacity: 0;
					}
				}

				&:before,
				&:after {
					top: 100%;
					border: solid transparent;
					height: 0;
					width: 0;
					position: absolute;
					left: 0;
					z-index: 1;
					margin: -1px 0 0;
				}

				&:after {
					border-color: rgba(196, 196, 196, 0);
					border-top-color: #E6E6E6;
					border-width: 20px 53px 0 52px;
					margin-left: 0;
				}

				&:before {
					border-color: rgba(122, 122, 122, 0);
					border-top-color: #fff;
					border-width: 20px 54px 0 54px;
					margin: 1px 0 0 -1px;
				}
			}
		}

		&_toggle {
			position: relative;

			@include media-breakpoint-up(md) {
				min-height: 118px;
				max-width: 180px;
				font-size: 3em;
			}

			@include media-breakpoint-up(lg) {
				max-width: 191px;
			}

			&:first-child {
				.steps__item__link {
					@include media-breakpoint-up(md) {
						padding: 14px;
					}
				}

				@include media-breakpoint-up(lg) {
					max-width: 242px;
				}
			}

			&:hover,
			&:focus {
				@include media-breakpoint-up(md) {
					.steps__item__link {
						background: #C4C4C4;
					}
				}
			}

			@include media-breakpoint-down(sm) {
				min-width: 100%;
				display: flex;
			}

			@include media-breakpoint-between(sm,sm) {
				margin: 0 -1rem;
			}
		}

		&_content {
			color: $blue;
			width: 100%;

			.btn {
				@include font-size(24px);
				padding: 0 1.6rem;
			}

			img {
				max-width: 100%;
				width: 100%;
			}

			ol,
			ul {
				padding: 0;
				margin: 0 0 15px;
			}

			ol {
				list-style: none;
				counter-reset: list-acc;

				li {
					&:before {
						counter-increment: list-acc;
						content: counter(list-acc) ". ";
						font-weight: bold;

					}
				}
			}

			@include media-breakpoint-down(sm) {
				h5 {
					display: none;
				}
			}

		}

		&__article {
			background: #C4C4C4;
			padding: 18px 15px;
			position: relative;
			z-index: 2;

			@include media-breakpoint-down(sm) {
				margin-top: 22px;
			}
		}

		&:first-child {
			.steps__item__link {
				.steps__item__counter {
					left: 21%;
				}

				@include media-breakpoint-down(md) {
					margin-top: 0;
				}

				@include media-breakpoint-up(md) {
					&:before,
					&:after {
						display: none;
					}
				}
			}

			@include media-breakpoint-down(sm) {
				.steps__item__text {
					padding: 20px 0 0 0;
				}
			}
		}

		&:nth-last-child(2) {
			@include media-breakpoint-down(sm) {
				.steps__item__link {
					&:after{
						border-color: #E6E6E6;
					}

					&:not(.collapsed) {
						&:after {
							border-color: transparent;
							border-top-color: #C4C4C4;
						}
					}
				}
			}
		}

		&:not(:first-child) {
			.steps__item__text {
				@include media-breakpoint-down(sm) {
					padding: 20px 0 0;
				}
			}
		}
	}
}

.packaging {
	background: #E6E6E6;

	&__paragraph {
		@include font-size(20px);
	}

	img {
		object-fit: cover;
	}
}

.slider {
	&-nav {
		.slick {
			&-slide {
				padding: 0 3px;
				// opacity: 0.6;
				transition: opacity .25s linear;
				outline: none;

				// &:hover {
				// 	opacity: 1;
				// }
			}

			&-list {
				margin: 0 -3px;
			}

			&-current {
				.slider__figure {
					border-color: $blue;
				}

				.slider__figure_thumbs {
					border-color:#2A2A2A;
				}
			}

			@include media-breakpoint-between(sm, md) {
				&-slide {
					padding: 0 6px;
				}

				&-list {
					margin: 0 -6px;
				}
			}

			@include media-breakpoint-down(md) {
				&-track {
					width: 100% !important;
					transform: none !important;
					height: auto !important;
					@include row-cols(4);

					.slick-cloned {
						display: none;
					}

					.slick-slide {
						padding: 1px 3px;
					}
				}
			}
		}

		.slick {
			&-arrow {
				z-index: 3;
				height: auto;
				width: auto;

				&:before {
					font-size: 32px;
				}
			}

			&-next {
				right: 15px;
			}

			&-prev {
				left: 15px;
			}

			@include media-breakpoint-down(sm) {
				&-next {
					right: -5px;
				}

				&-prev {
					left: -5px;
				}
			}
		}
	}

	&__figure {
		border: 1px solid #E6E6E6;
		margin: 0;
		position: relative;

		&_thumbs {
			position: relative;

			&:before {
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				content: '';
				opacity: 0;
				background: #0E3154;
				mix-blend-mode: multiply;
				transition: opacity 0.25s linear;
				z-index: 1;
			}

			&:hover {
				&:before {
					opacity: 0.6;
				}
			}
		}
	}

	&__img {
		&_main {
			height: 260px;
			object-fit: cover;
		}

		&-caption {
			position: absolute;
			bottom: 15px;
			background: $blue;
			color: #fff;
			padding: 5px;
			display: block;
			@include font-size(18px);
			border: 1px solid #fff;

			@include media-breakpoint-up(lg) {
				left: 0;
			}

			@include media-breakpoint-down(md) {
				right: 15px;
				margin-left: 15px;
			}
		}
	}
}
