// stylelint-disable scss/dollar-variable-empty-line-before
// BASE
$font-family-base: 'Eurostile',
Arial,
sans-serif;
$font-size-base: 14px / 16px * 1rem;
$body-color: #000;
$enable-responsive-font-sizes: true;

// COLORS
$color-black: #000;
$color-white: #fff;
$blue: #0E3154;
$light: #d7dbde;

// FONTS
$h5-font-size: 18;
$headings-margin-bottom: 15px;
$headings-font-weight: 700;
$headings-color: $blue;
$h2-font-size: 48px;

// BUTTONS
$btn-border-radius: 0;
$btn-border-radius-sm: 0;
$btn-border-radius-lg: 2px;
$btn-font-size-sm: 14;
$btn-font-size-lg: 16;
$btn-padding-y: 0.32rem;
$btn-padding-x: 0.71rem;
$btn-padding-y-sm: 0.22rem;
$btn-padding-x-sm: 0.77rem;
$btn-padding-x-lg: 1.75rem;

// Grid
$container-max-widths: (sm: 540px,
	md: 720px,
	lg: 960px);

$grid-gutter-width: 30px;

// tabs
$nav-tabs-link-active-bg: #D8D7D3;
$nav-tabs-link-bg: #F2F1EF;
$nav-tabs-link-active-border-color: white;
$nav-tabs-link-border-color: white;
$nav-tabs-link-active-color: $blue;
$nav-tabs-border-radius: 0;
$nav-tabs-border-width: 0;
$nav-tabs-divider-width: 1px;

// inputs
$input-font-size-lg: 18px;
$input-border-radius: 0;
$input-border-radius-lg: 0;
$input-placeholder-color: #aaa;
$input-height: 31px;
$input-height-lg: 40px;
$input-border-color: #2A2A2A;
$input-border-color-lg: #E6E6E6;
$input-focus-box-shadow: 0 0 5px 0.1rem rgba(14, 49, 84, 0.25);
$input-group-addon-border-color: $input-border-color-lg;
$input-group-addon-bg: none;
$custom-control-indicator-size: 1.4rem;
$custom-checkbox-indicator-border-radius: 0;
$custom-control-indicator-border-color: #000;
$custom-select-focus-box-shadow: $input-focus-box-shadow;
$input-line-height-lg: 1.2;

// form
$form-group-margin-bottom: 1.15rem;
$form-feedback-font-size: 14;

// progress
$progress-bg: #E6E6E6;
$progress-bg-active: #C4C4C4;

// dropdown
$dropdown-border-color: #2A2A2A;
$dropdown-border-radius: 0;
$dropdown-item-padding-y: 0;
$dropdown-item-padding-x: 39px;
$dropdown-header-padding: 0 12px;
$dropdown-header-color: #000;
$dropdown-spacer: -1px;

// pagination
$pagination-border-width: 0;
$pagination-hover-bg: $blue;
$pagination-hover-color: white;
$pagination-color: #000;
$pagination-padding-y: 0;
$pagination-padding-x: 0;
$pagination-focus-box-shadow: $input-focus-box-shadow;

// modals
$modal-content-border-radius: 0;
$modal-title-line-height: 1.2;
$modal-dialog-margin-y-sm-up: 65px;

// slick
$slick-font-path: "../fonts/slick/";
$slick-arrow-color: $blue;
$slick-opacity-default: 1;
